/* Common Css */
* {
  box-sizing: border-box;
}

:root {
  --text-color: hsl(200, 15%, 8%);
  --background-color: hsl(0, 0%, 98%);
  --elements-color: hsl(0, 0%, 100%);
}

#country-container.dark-mode {
  --text-color: hsl(0, 0%, 100%);
  --background-color: hsl(207, 26%, 17%);
  --elements-color: hsl(209, 23%, 22%);
  background-color: var(--background-color);
}

body {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans", sans-serif;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.skeleton {
  animation: skeleton-loading 1s linear infinite alternate;
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 70%);
  }

  100% {
    background-color: hsl(200, 20%, 95%);
  }
}

.detail-skeleton-img {
  width: 500px;
  height: 333px;
  border-radius: 0.3rem;
}

.detail-skeleton-heading {
  width: 100%;
  margin-bottom: 1rem;
  height: 2.5em;
  border-radius: 0.3rem;
}

.detail-skeleton-text {
  display: inline-block;
  width: 80%;
  height: 1rem;
  border-radius: 0.3rem;
  margin-block: 1em;
  margin-inline: auto;
}

.load-skeleton {
  width: 100%;
  max-width: 400px;
}

.load-skeleton .countries {
  width: 100%;
  max-width: 200px;
}

/* Header Section Start */
#header_section {
  background-color: var(--elements-color);
  color: var(--text-color);
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.2);
  padding: 0 70px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  max-width: 1300px;
  margin-inline: auto;
}

.icon-container {
  display: flex;
  align-items: center;
  gap: 7px;
  cursor: pointer;
  font-weight: 600;
}

/* Header Section End */

/* Country Details Section Start */
#country-container {
  padding: 50px 70px;
  min-width: 100%;
  min-height: calc(100vh - 85px);
}

.button-container {
  max-width: 1300px;
  margin-inline: auto;
}

#back_btn {
  cursor: pointer;
  width: 130px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: var(--text-color);
  background-color: var(--elements-color);
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 7px;
  font-weight: 500;
}

.country-details {
  margin: 70px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 70px;
  max-width: 1300px;
  margin-inline: auto;
}

.country-details img {
  width: 100%;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.country-image-container {
  width: 100%;
  max-width: 500px;
}

.countryImg {
  opacity: 1;
}

.details_container {
  display: flex;
  gap: 70px;
  color: var(--text-color);
  font-weight: 700;
}

.details_container span {
  font-weight: 400;
}

#countryName {
  color: var(--text-color);
  font-size: 1.5rem;
  margin: 0;
  font-weight: 800;
}

.left-details,
.right-details {
  width: 100%;
}

.border-countries {
  color: var(--text-color);
  display: flex;
  align-items: center;
  font-weight: 700;
  gap: 20px;
  margin-top: 2rem;
}

.border-countries p {
  align-self: flex-start;
}

.countries {
  display: flex;
  align-items: center;
  gap: 15px;
  text-align: center;
  flex-wrap: wrap;
}

.load-skeleton .countries {
  flex-wrap: unset;
}

.countries a {
  padding: 7px 20px;
  color: var(--text-color);
  background-color: var(--elements-color);
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 5px;
  font-weight: 400;
  transition: transform 0.3s ease;
}

.countries a:hover {
  transform: scale(1.03);
}

/* Country Details Section End */

/* Media Queries Section Start */

@media (max-width: 1168px) {
  .details_container {
    gap: 50px;
  }
}

@media (max-width: 968px) {
  #country-container {
    padding: 50px;
  }

  .country-details {
    gap: 50px;
    flex-direction: column;
  }

  .details_container {
    flex-direction: column;
    gap: 20px;
  }

  .border-countries {
    align-items: unset;
    flex-direction: column;
    margin-top: 1rem;
  }
}

@media (max-width: 768px) {
  #header_section {
    padding: 0px 50px;
  }

  #header_section h1 {
    font-size: 1.5rem;
  }

  .icon-container {
    font-size: 14px;
  }

  .countries {
    flex-wrap: wrap;
  }

  .border-countries {
    margin-top: 25px;
  }

  .load-skeleton .countries {
    flex-wrap: unset;
  }
}

@media (max-width: 468px) {
  #header_section {
    padding: 10px 20px;
  }

  #header_section h1 {
    font-size: 1rem;
  }

  .icon-container {
    font-size: 12px;
  }

  .border-countries {
    align-items: initial;
    flex-direction: column;
  }

  .load-skeleton {
    width: 100%;
    max-width: 200px;
  }
}

/* Media Queries Section End */
